<template>
  <MyTemplate>
    <template v-slot:myContent>
      <b-row>
        <!-- progressbar -->
        <div class="col-md-12">
          <div class="progressbar-container text-center">
            <ul class="progressbar">
              <li class="progressbar-done">檢視購物車</li>
              <li class="progressbar-done">付款方式及寄送資訊</li>
              <li class="progressbar-active">確認訂單資料</li>
              <li>訂單成立</li>
            </ul>
          </div>
        </div>
        <!-- progressbar -->
      </b-row>

      <b-row class="mt-3">
        <b-col md="12">
          <b-card
            header="確認購物清單 "
            header-text-variant="dark"
            header-tag="header"
            header-bg-variant="muted"
          >
            <b-table
              stacked="lg"
              ref="selectableTable"
              :fields="fields"
              :items="items"
            >
              <!-- Optional default data cell scoped slot -->
              <template v-slot:cell()="data">
                <span v-text="data.value"></span>
              </template>
              <!-- A custom formatted column -->
              <template v-slot:cell(name)="data">
                <a class="text-decoration-none text-primary" href="#">
                  <span v-text="data.value"></span>
                </a>
              </template>
              <!-- A custom formatted column -->
              <template v-slot:cell(salePrice)="data">
                <span v-text="salePrice(data)"></span>
              </template>
              <!-- A custom formatted column -->
              <template v-slot:cell(subtotal)="data">
                <span v-text="calSubTotal(data)"></span>
              </template>
            </b-table>
            <div class="d-flex bg-light">
              <div class="ml-auto">
                <p class="mt-3 mr-3">
                  共
                  <span class="text-danger">{{ items.length }}</span>
                  項商品，數量
                  <span class="text-danger" v-text="totalQuantity()"></span>
                  個，總金額 NT$
                  <span class="text-danger" v-text="totalAmount()"></span> 元
                </p>
              </div>
            </div>
          </b-card>
        </b-col>
      </b-row>

      <b-row class="mt-3">
        <b-col md="12">
          <b-card
            header="付款方式及寄送資訊"
            header-text-variant="dark"
            header-tag="header"
            header-bg-variant="muted"
          >
            <b-row class>
              <b-col md="2" class="d-flex align-items-center ml-1">
                <label>配送方式</label>
              </b-col>
              <b-col md="9" class="ml-1">
                <span>郵寄</span>
              </b-col>
            </b-row>

            <b-row class>
              <b-col md="2" class="d-flex align-items-center ml-1">
                <label>付款方式</label>
              </b-col>
              <b-col md="9" class="ml-1">
                <span>貨到付款</span>
              </b-col>
            </b-row>

            <b-row class>
              <b-col md="2" class="d-flex align-items-center ml-1">
                <label>發票資訊</label>
              </b-col>
              <b-col md="9" class="ml-1">
                <span>紙本發票</span>
              </b-col>
            </b-row>

            <b-row class>
              <b-col md="2" class="d-flex align-items-center ml-1">
                <label>訂購人</label>
              </b-col>
              <b-col md="9" class="ml-1">
                <span>訂購人姓名</span>
              </b-col>
            </b-row>

            <b-row class>
              <b-col md="2" class="d-flex align-items-center ml-1">
                <label>聯絡電話</label>
              </b-col>
              <b-col md="9" class="ml-1">
                <span>訂購人聯絡電話</span>
              </b-col>
            </b-row>

            <b-row class>
              <b-col md="2" class="d-flex align-items-center ml-1">
                <label for="buyer-address">地址</label>
              </b-col>
              <b-col md="9" class="ml-1">
                <span>訂購人地址</span>
              </b-col>
            </b-row>

            <b-row class>
              <b-col md="2" class="d-flex align-items-center ml-1">
                <label>收件人</label>
              </b-col>
              <b-col md="9" class="ml-1">
                <span>收件人姓名</span>
              </b-col>
            </b-row>

            <b-row class>
              <b-col md="2" class="d-flex align-items-center ml-1">
                <label>收件人聯絡電話</label>
              </b-col>
              <b-col md="9" class="ml-1">
                <span>收件人聯絡電話</span>
              </b-col>
            </b-row>

            <b-row class>
              <b-col md="2" class="d-flex align-items-center ml-1">
                <label for="buyer-address">收件人地址</label>
              </b-col>
              <b-col md="9" class="ml-1">
                <span>收件人地址</span>
              </b-col>
            </b-row>
          </b-card>

          <div class="d-flex justify-content-end mt-3">
            <b-button class="mr-3" variant="outline-primary">
              <font-awesome-icon
                :icon="['fas', 'angle-double-left']"
              />&nbsp;上一步
            </b-button>
            <b-button type="submit" class="mr-3" variant="outline-primary">
              確認送出&nbsp;
              <font-awesome-icon :icon="['fas', 'angle-double-right']" />
            </b-button>
          </div>
        </b-col>
      </b-row>
    </template>
  </MyTemplate>
</template>
 
<script>
import MyTemplate from "@/components/MyTemplate.vue";
import styles from "@/assets/css/progress-bar.css";
import VueNumericInput from "vue-numeric-input";
import { library } from "@fortawesome/fontawesome-svg-core";
import {
  faAngleDoubleRight,
  faAngleDoubleLeft,
} from "@fortawesome/free-solid-svg-icons";

library.add(faAngleDoubleRight, faAngleDoubleLeft);

export default {
  name: "Confirm",
  data() {
    return {
      // https://bootstrap-vue.org/docs/components/table#row-select-support
      // selectMode: "multi",
      // onRowSelected(items) {
      //   this.selected = items
      // },
      allSelected: false,
      fields: [
        // A column that needs custom formatting
        { key: "name", label: "商品名稱", thClass: "bg-LightSteelBlue" },
        { key: "listPrice", label: "定價(NT$)", thClass: "bg-LightSteelBlue" },
        { key: "salePrice", label: "售價(NT$)", thClass: "bg-LightSteelBlue" },
        { key: "quantity", label: "數量", thClass: "bg-LightSteelBlue" },
        { key: "subtotal", label: "小計(NT$)", thClass: "bg-LightSteelBlue" },
      ],
      items: [
        {
          name: "架構模式｜使用 Python",
          src: require("@/assets/img/9789865025960.jpg"),
          listPrice: 680,
          discount: 79,
          quantity: 1,
          checked: false,
        },
        {
          name: "架構模式｜使用 Python",
          src: require("@/assets/img/9789865025960.jpg"),
          listPrice: 680,
          discount: 79,
          quantity: 3,
          checked: false,
        },
        {
          name: "架構模式｜使用 Python",
          src: require("@/assets/img/9789865025960.jpg"),
          listPrice: 680,
          discount: 79,
          quantity: 5,
          checked: false,
        },
        {
          name: "架構模式｜使用 Python",
          src: require("@/assets/img/9789865025960.jpg"),
          listPrice: 680,
          discount: 79,
          quantity: 7,
          checked: false,
        },
      ],
    };
  },
  components: {
    MyTemplate,
    VueNumericInput,
  },
  methods: {
    toggleAll(checked) {
      //this.selected = checked ? this.flavours.slice() : []
      //console.log(checked);
      this.items.forEach(function (item) {
        item.checked = checked;
        //console.log("checked: " + checked);
      });
      // this.$refs.selectableTable.refresh();
    },
    salePrice(data) {
      // return Math.round(data.item.listPrice * data.item.discount / 100);
      data.item.salePrice = this.calSalePrice(
        data.item.listPrice,
        data.item.discount
      );
      return data.item.salePrice;
    },
    calSalePrice(price, discount) {
      return Math.round((price * discount) / 100);
    },
    calSubTotal(data) {
      // return this.salePrice(data) * data.item.quantity;
      return data.item.salePrice * data.item.quantity;
    },
    changeQuantity(index) {
      console.log("index: " + index);
      console.log("quantity: " + this.items[index].quantity);
      if (isNaN(this.items[index].quantity)) {
        //this.items[index].quantity = 1;
        alert("請輸入正確數量");
        //this.$refs.selectableTable.refresh();
        return;
      }
      if (
        Number.isInteger(this.items[index].quantity) &&
        this.items[index].quantity < 1
      ) {
        alert("數量最小為 1");
        this.items[index].quantity = 1;
        return;
      }
    },
    deleteItem(index) {
      console.log("index: " + index);
      console.log("quantity: " + this.items[index].quantity);
      this.items.splice(index, 1);
    },
    deleteSelected() {
      // let removeIndexes = [];
      // for (let i = 0; i < this.items.length; i++) {
      //   if (this.items[i].checked == true) {
      //     removeIndexes.push(i);
      //   }
      // }
      // console.log("removeIndexes: " + removeIndexes);
      // for (let i = removeIndexes.length - 1; i >= 0; i--) {
      //   this.items.splice(removeIndexes[i], 1);
      // }

      for (let i = this.items.length - 1; i >= 0; i--) {
        if (this.items[i].checked == true) {
          this.items.splice(i, 1);
        }
      }
      this.allSelected = false;
    },
    totalQuantity() {
      //https://fred-zone.blogspot.com/2017/01/javascript-mapreduce.html
      //https://developer.mozilla.org/zh-TW/docs/Web/JavaScript/Reference/Global_Objects/Array/map
      //https://developer.mozilla.org/zh-TW/docs/Web/JavaScript/Reference/Global_Objects/Array/Reduce
      return this.items
        .map((item) => parseInt(item.quantity, 10))
        .reduce((acc, cur) => acc + cur, 0);
    },
    totalAmount() {
      // return this.items.map(item => Math.round(item.listPrice * item.discount / 100) * parseInt(item.quantity, 10)).reduce((acc, cur) => acc + cur, 0);
      return this.items
        .map(
          (item) =>
            this.calSalePrice(item.listPrice, item.discount) *
            parseInt(item.quantity, 10)
        )
        .reduce((acc, cur) => acc + cur, 0);
    },
  },
  mounted() {},
};
</script>
<style>
.bg-LightSteelBlue {
  background-color: LightSteelBlue !important;
}
</style>